body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 70px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.customerForm {
  padding: 50px;
}

.customerInfo {
  display: flex;
  justify-content: space-evenly;
}

input[type="text"] {
  display: block;
  margin: 20px;
}
.media {
    height: 140px;
}

.card {
    width: 345px;
    margin: 25px;
}
.pizzaContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

img {
    width: 250px;
}

.pizzaItem {
    width: 300px;
}

button {
    font-size: 3em;
}
